import { forwardRef } from 'react';

import { useDesignSystemTheme } from '../Hooks';
import { CheckIcon, InfoIcon } from '../Icon';
import { Tooltip } from '../Tooltip';
import type { HTMLDataAttributes } from '../types';
import { useDialogComboboxContext } from './hooks/useDialogComboboxContext';
import { useDialogComboboxOptionListContext } from './hooks/useDialogComboboxOptionListContext';
import { getDialogComboboxOptionItemWrapperStyles, infoIconStyles } from './shared';

export interface DialogComboboxOptionListSelectItemProps
  extends HTMLDataAttributes,
    React.HTMLAttributes<HTMLDivElement> {
  value: string;
  checked?: boolean;
  disabled?: boolean;
  disabledReason?: React.ReactNode;
  children?: React.ReactNode;
  onChange?: (...args: any[]) => any;
}

export const DialogComboboxOptionListSelectItem = forwardRef<HTMLDivElement, DialogComboboxOptionListSelectItemProps>(
  ({ value, checked, disabledReason, onChange, children, ...props }, ref) => {
    const { theme } = useDesignSystemTheme();
    const { stayOpenOnSelection, setIsOpen, value: existingValue } = useDialogComboboxContext();
    const { isInsideDialogComboboxOptionList } = useDialogComboboxOptionListContext();

    if (!isInsideDialogComboboxOptionList) {
      throw new Error('`DialogComboboxOptionListSelectItem` must be used within `DialogComboboxOptionList`');
    }

    const handleSelect = () => {
      if (onChange) {
        onChange(value);

        // On selecting a previously selected value, manually close the popup, top level logic will not be triggered
        if (!stayOpenOnSelection && existingValue?.includes(value)) {
          setIsOpen(false);
        }
      }
    };

    let content: React.ReactNode = children ?? value;
    if (props.disabled && disabledReason) {
      content = (
        <>
          {content}
          <Tooltip title={disabledReason} placement="right">
            <span css={infoIconStyles(theme)}>
              <InfoIcon />
            </span>
          </Tooltip>
        </>
      );
    }

    return (
      <div
        ref={ref}
        css={[
          getDialogComboboxOptionItemWrapperStyles(theme),
          {
            '&:focus': {
              background: theme.colors.actionTertiaryBackgroundHover,
              outline: 'none',
            },
          },
        ]}
        {...props}
        onClick={(e) => {
          if (props.disabled) {
            e.preventDefault();
          } else {
            handleSelect();
          }
        }}
        tabIndex={0}
        onKeyDown={(e) => {
          switch (e.key) {
            case 'ArrowDown':
              e.preventDefault();
              const nextSibling = e.currentTarget.nextElementSibling as HTMLElement;
              nextSibling?.focus();
              break;
            case 'ArrowUp':
              e.preventDefault();
              const previousSibling = e.currentTarget.previousElementSibling as HTMLElement;
              previousSibling?.focus();
              break;
            case ' ':
            case 'Enter':
              e.preventDefault();
              handleSelect();
              break;
          }
        }}
        role="option"
        aria-selected={checked}
        aria-label={value}
      >
        {checked ? <CheckIcon /> : <div css={{ width: 16 }} />}
        <label
          style={{
            fontSize: theme.typography.fontSizeBase,
            fontStyle: 'normal',
            fontWeight: 400,
            marginLeft: theme.spacing.sm,
            cursor: 'pointer',
          }}
        >
          {content}
        </label>
      </div>
    );
  }
);
