import { forwardRef } from 'react';

import { Checkbox } from '../Checkbox';
import { useDesignSystemTheme } from '../Hooks';
import { InfoIcon } from '../Icon';
import { Tooltip } from '../Tooltip';
import type { HTMLDataAttributes } from '../types';
import { useDialogComboboxOptionListContext } from './hooks/useDialogComboboxOptionListContext';
import { getDialogComboboxOptionItemWrapperStyles, infoIconStyles } from './shared';

export interface DialogComboboxOptionListCheckboxItemProps
  extends HTMLDataAttributes,
    React.HTMLAttributes<HTMLDivElement> {
  value: string;
  checked?: boolean;
  disabled?: boolean;
  disabledReason?: React.ReactNode;
  indeterminate?: boolean;
  children?: React.ReactNode;
  onChange?: (...args: any[]) => any;
}

export const DialogComboboxOptionListCheckboxItem = forwardRef<
  HTMLDivElement,
  DialogComboboxOptionListCheckboxItemProps
>(({ value, checked, indeterminate, onChange, children, disabledReason, ...props }, ref) => {
  const { theme } = useDesignSystemTheme();
  const { isInsideDialogComboboxOptionList } = useDialogComboboxOptionListContext();

  if (!isInsideDialogComboboxOptionList) {
    throw new Error('`DialogComboboxOptionListCheckboxItem` must be used within `DialogComboboxOptionList`');
  }

  const handleSelect = () => {
    if (onChange) {
      onChange(value);
    }
  };

  let content: React.ReactNode = children ?? value;
  if (props.disabled && disabledReason) {
    content = (
      <>
        {content}
        <Tooltip title={disabledReason} placement="right">
          <span css={infoIconStyles(theme)}>
            <InfoIcon />
          </span>
        </Tooltip>
      </>
    );
  }

  return (
    <div
      ref={ref}
      role="option"
      // Using aria-selected instead of aria-checked because the parent listbox
      aria-selected={indeterminate ? false : checked}
      css={[getDialogComboboxOptionItemWrapperStyles(theme)]}
      {...props}
      onClick={(e) => {
        if (props.disabled) {
          e.preventDefault();
        } else {
          handleSelect();
        }
      }}
    >
      <Checkbox
        disabled={props.disabled}
        isChecked={indeterminate ? null : checked}
        css={{
          pointerEvents: 'none',
          '& > label': {
            fontSize: theme.typography.fontSizeBase,
            fontStyle: 'normal',
            fontWeight: 400,
          },
        }}
        // Needed because Antd handles keyboard inputs as clicks
        onClick={(e) => {
          e.stopPropagation();
          handleSelect();
        }}
      >
        {content}
      </Checkbox>
    </div>
  );
});
